            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
    showDetail();
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>  
<style scoped>
